/** SASS Varibales **/
/** CGP Styles **/
.cgp-mega-menu-wrap ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.cgp-mega-menu-wrap ul li {
  list-style: none;
  margin: 0;
}
.cgp-mega-menu-wrap ul li .fas {
  margin-left: 0.25em;
}
.cgp-mega-menu-wrap ul a {
  text-decoration: none;
}
.cgp-mega-menu-wrap .cgp-mega-menu-mobile-logo {
  align-items: center;
  display: none;
  justify-content: left;
  height: 100px;
  width: 70%;
}
.cgp-mega-menu-wrap .cgp-mega-menu-mobile-logo img {
  display: block;
}
.cgp-mega-menu-wrap .cgp-mega-menu-toggle {
  display: none;
  position: absolute;
  top: 26px;
  right: 20px;
  z-index: 1;
  background: #fff !important;
  border: 0;
  color: #2b3035 !important;
  font-size: 40px;
  line-height: 40px;
  height: 40px;
  width: 40px;
  margin: 0;
  padding: 0;
}
.cgp-mega-menu-wrap .cgp-mega-menu-toggle:hover {
  background: #fff !important;
  color: #2b3035 !important;
}
.cgp-mega-menu-wrap .cgp-mega-menu {
  background-color: #fff;
  color: #0f7d21;
  font-family: "Noto Sans", sans-serif;
  font-size: 1em;
  font-weight: 600;
  margin: 0;
  padding: 0;
  position: relative;
}
.cgp-mega-menu-wrap .cgp-mega-menu > .menu-item > a {
  display: block;
  color: #0f7d21;
  text-transform: uppercase;
}
.cgp-mega-menu-wrap .cgp-mega-menu > .menu-item > a:hover, .cgp-mega-menu-wrap .cgp-mega-menu > .menu-item > a:focus {
  background-color: #f9f9f9;
  text-decoration: underline;
}
.cgp-mega-menu-wrap .cgp-mega-menu .sub-menu-item {
  font-weight: 400;
}
.cgp-mega-menu-wrap .cgp-mega-menu .sub-menu-item a {
  color: #54595f;
  display: block;
}
.cgp-mega-menu-wrap .cgp-mega-menu .sub-menu-item a:hover, .cgp-mega-menu-wrap .cgp-mega-menu .sub-menu-item a:focus {
  text-decoration: underline;
}
.cgp-mega-menu-wrap .cgp-mega-menu .cgp-mega-menu-heading.sub-menu-item > h2 {
  color: #2b3035;
  font-weight: 400;
  margin: 0;
  padding: 0 0 10px 0;
}
.cgp-mega-menu-wrap .cgp-mega-menu .cgp-mega-menu-heading.sub-menu-item > h2 a {
  padding: 0;
}
.cgp-mega-menu-wrap .cgp-mega-menu .cgp-mega-menu-item-spacer {
  margin-top: 20px;
}
@media screen and (max-width: 959px) {
  .cgp-mega-menu-wrap .cgp-mega-menu-mobile-logo {
    display: flex;
  }
  .cgp-mega-menu-wrap .cgp-mega-menu-toggle {
    display: block;
  }
  .cgp-mega-menu-wrap .cgp-mega-menu-toggle ~ .cgp-mega-menu {
    height: 0;
  }
  .cgp-mega-menu-wrap .cgp-mega-menu-toggle ~ .cgp-mega-menu > .menu-item {
    height: 0;
    overflow: hidden;
    visibility: collapse;
  }
  .cgp-mega-menu-wrap .cgp-mega-menu-toggle[aria-expanded=true] ~ .cgp-mega-menu {
    height: auto;
    z-index: 2;
    margin-top: 20px;
  }
  .cgp-mega-menu-wrap .cgp-mega-menu-toggle[aria-expanded=true] ~ .cgp-mega-menu > .menu-item {
    height: auto;
    overflow: visible;
    visibility: visible;
  }
  .cgp-mega-menu-wrap .cgp-mega-menu-toggle[aria-expanded=true] .fa-bars::before {
    content: "\f00d";
  }
  .cgp-mega-menu-wrap .cgp-mega-menu > .menu-item {
    border-bottom: 1px solid #e9e9e9;
  }
  .cgp-mega-menu-wrap .cgp-mega-menu > .menu-item a {
    padding: 20px;
  }
  .cgp-mega-menu-wrap .cgp-mega-menu .cgp-mega-menu-home .cgp-mega-menu-has-logo {
    display: none;
  }
  .cgp-mega-menu-wrap .cgp-mega-menu .cgp-mega-menu-home .cgp-mega-menu-has-logo:hover {
    background: transparent;
  }
  .cgp-mega-menu-wrap .cgp-mega-menu .sub-menu-panel {
    visibility: hidden;
    top: -9999em;
    max-height: 0px;
    overflow: hidden;
    opacity: 0;
    transition: opacity 250ms ease 250ms, max-height 500ms ease, visibility 0s linear 500ms, top 0s linear 500ms;
  }
  .cgp-mega-menu-wrap .cgp-mega-menu .sub-menu-panel.open {
    visibility: visible;
    opacity: 1;
    z-index: 1001;
    max-height: none;
    transition: opacity 250ms ease, max-height 500ms ease, visibility 0s linear 0s, top 0s linear 0s;
  }
  .cgp-mega-menu-wrap .cgp-mega-menu .sub-menu-panel .sub-menu-group {
    border-top: 1px solid #e9e9e9;
  }
  .cgp-mega-menu-wrap .cgp-mega-menu .sub-menu-panel .sub-menu-group .sub-menu-item {
    border-bottom: 1px solid #e9e9e9;
  }
  .cgp-mega-menu-wrap .cgp-mega-menu .sub-menu-panel .sub-menu-group .sub-menu-item:last-child {
    border-bottom: none;
  }
  .cgp-mega-menu-wrap .cgp-mega-menu .sub-menu-panel .sub-menu-group .sub-menu-item a.cgp-menu-tooltip .fas {
    display: none;
  }
  .cgp-mega-menu-wrap .cgp-mega-menu .sub-menu-panel .sub-menu-group .sub-menu-item .sub-menu-group .sub-menu-item a {
    padding-left: 40px;
  }
  .cgp-mega-menu-wrap .cgp-mega-menu .sub-menu-panel .sub-menu-group .cgp-mega-menu-heading.sub-menu-item > h2 {
    font-size: 1em;
    padding: 20px;
  }
  .cgp-mega-menu-wrap .cgp-mega-menu .sub-menu-panel .sub-menu-group .cgp-mega-menu-heading.sub-menu-item > h2 a {
    padding: 0;
  }
}
@media screen and (min-width: 960px) {
  .cgp-mega-menu-wrap {
    position: relative;
    z-index: 999;
  }
  .cgp-mega-menu-wrap .cgp-mega-menu {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 100px;
  }
  .cgp-mega-menu-wrap .cgp-mega-menu > .menu-item {
    flex: 0 0 auto;
    align-self: center;
    height: 100px;
    line-height: 100px;
  }
  .cgp-mega-menu-wrap .cgp-mega-menu > .menu-item > a {
    height: 100px;
    padding: 0 20px;
  }
  .cgp-mega-menu-wrap .cgp-mega-menu .cgp-mega-menu-home {
    margin-right: auto;
  }
  .cgp-mega-menu-wrap .cgp-mega-menu .cgp-mega-menu-home .cgp-mega-menu-has-logo {
    align-items: center;
    display: flex;
    justify-content: center;
    padding: 0;
  }
  .cgp-mega-menu-wrap .cgp-mega-menu .cgp-mega-menu-home .cgp-mega-menu-has-logo:hover {
    background: transparent;
  }
  .cgp-mega-menu-wrap .sub-menu-panel {
    visibility: hidden;
    transition: opacity 250ms ease 250ms, max-height 250ms ease, visibility 0s linear 250ms, top 0s linear 250ms;
    width: 100%;
    max-width: 1230px;
    position: absolute;
    left: 0;
    top: 90px;
    background-color: #f9f9f9;
    padding: 10px;
  }
  .cgp-mega-menu-wrap .sub-menu-panel.open {
    visibility: visible;
    z-index: 1000;
  }
  .cgp-mega-menu-wrap .sub-menu-group {
    padding: 10px 0;
  }
  .cgp-mega-menu-wrap .sub-menu-group .sub-menu-item {
    height: auto;
    line-height: 1.2em;
  }
  .cgp-mega-menu-wrap .sub-menu-group .sub-menu-item a {
    padding: 5px 0;
  }
  .cgp-mega-menu-wrap .sub-menu-group .sub-menu-item a.cgp-menu-tooltip {
    position: relative;
  }
  .cgp-mega-menu-wrap .sub-menu-group .sub-menu-item a.cgp-menu-tooltip[aria-label]:after {
    border-radius: 10px;
    opacity: 0;
    content: attr(aria-label);
    font-size: 12px;
    font-weight: 400;
    padding: 10px;
    position: absolute;
    left: 40%;
    top: 0;
    z-index: 20;
    background: rgba(0, 0, 0, 0.75);
    color: #fff;
    width: 200px;
    transition: opacity 0.25s;
    pointer-events: none;
  }
  .cgp-mega-menu-wrap .sub-menu-group .sub-menu-item a.cgp-menu-tooltip[aria-label]:hover:after {
    opacity: 1;
    transition-delay: 0.5s;
  }
  .cgp-mega-menu-wrap .sub-menu-group .sub-menu-item a.cgp-menu-tooltip .fas {
    color: #515aa9;
  }
  .cgp-mega-menu-wrap .cgp-menu-cols .sub-menu-group {
    display: flex;
    flex-direction: row;
  }
  .cgp-mega-menu-wrap .cgp-menu-cols .sub-menu-group .sub-menu-item.cgp-mega-menu-heading {
    flex: 1 0 auto;
    padding: 20px 20px 0 20px;
  }
  .cgp-mega-menu-wrap .cgp-menu-cols .sub-menu-group .sub-menu-item.cgp-mega-menu-heading > h2 {
    border-bottom: 1px solid #e9e9e9;
    font-size: 1.125em;
    text-transform: uppercase;
  }
  .cgp-mega-menu-wrap .cgp-menu-cols .sub-menu-group .sub-menu-item.cgp-mega-menu-heading > h2 a {
    padding: 0;
  }
  .cgp-mega-menu-wrap .cgp-menu-cols .sub-menu-group .sub-menu-item .sub-menu-group {
    flex-direction: column;
  }
  .cgp-mega-menu-wrap .cgp-menu-row .sub-menu-group {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    padding: 20px;
  }
  .cgp-mega-menu-wrap .cgp-menu-row .sub-menu-group .sub-menu-item {
    margin-right: 20px;
  }
}